.card {
  background-color: rgba(155, 255, 255, 0.26);
  padding: 20px;
  border-radius: 10px;
}

a,
button {
  margin: 10px;
}

span {
  margin-right: 20px;
}

h3 {
  font-style: bold;
  text-decoration: underline solid rgb(68, 68, 68);
  margin: 0 0 20px 20px;
}

h5 {
  color: rgb(75, 75, 75);
}
